<template id="myIndex">

    <div style="position: relative">
        <v-scroll :on-refresh="onRefresh" :on-infinite="onInfinite">
            <div class="myIndex_box">
                <div class="banner index_banner">
                    <v-banner></v-banner>
                    <div class="userHeaderImg">
                        <img :src="user.faceUrl" alt="">
                    </div>
                </div>
                <!--banner end -->

                <div class="chart_box">
                    <v-chart :chartData="chartData"></v-chart>
                </div>

                <div class="myMood_list" v-for="( item,index)  in downdata" :key="index" v-show="!item.hide">

                    <img class="moodImg" :src="item.moodValueUrl" alt="">
                    <div class="moodImg_right">
                        <div class="moodState"
                             :class="{'unhappy_txt_color':item.moodValue<=4,'happy_txt_color':item.moodValue>4}">
                            {{item.moodValueText}}
                        </div>

                        <template v-if="currTime-item.addTime<=20*60 && (item.content=='' || item.content==null)  ">
                            <router-link :to="item.editLink" class="editMood">
                                20分钟内可以补充文字和图片
                                <img src="../images/bianji.png" alt="">
                            </router-link>
                        </template>
                        <template v-if=" (item.content!=''&& item.content!=null)  ">
                            <div class="moodContext">{{item.content}}</div>
                            <div class="moodPhotoLists" v-if="item.haspicture">
                                <div class="moodPhotoList" v-for="pic in item.pics">
                                    <img :src="pic.smallUrl" :data-bigPic="pic.bigUrl" :data-w="pic.picwidth"
                                         :data-h="pic.picheight" :style="pic.styleObject"
                                         @click="showBigImg(item.pics,pic)">
                                </div>

                            </div>
                        </template>

                        <div class="moodLoc" v-if="item.content!=''&& item.content!=null">{{item.address}}</div>
                        <div class="moodTime">
                            <span>{{item.outTime}}</span>
                            <span v-if="currTime-item.addTime<=180 &&(item.content=='' || item.content==null)"
                                  class="btn_del" @click="revoke(item.id,index)">撤回</span>
                            <span v-if="item.content!='' && item.content!=null" class="btn_del"
                                  @click="empty(item.id,index)">删除</span>
                            <div class="moodFollow" @click="showComment(item.id,index)">
                                <span class="followCount">{{item.careCount}}</span>
                                <img class="followtype" :src="item.careImg" alt="">
                                <template v-if="item.content!==null&& item.content!==''">
                                    <span class="followCount">{{item.replycount}}</span>
                                    <img class="followtype" src="../images/comments.png"
                                         style="width: 18px;margin-top: 3px;" alt="">
                                </template>
                            </div>
                        </div>
                    </div>
                    <div class="show_box" v-if="item.hasComments && item.isShowComment">
                        <div class="arraw"></div>
                        <div class="show_top">
                            <img class="show_img1" src="../images/list_dianz_pre.png"/>
                            <img v-for="care in item.careList" :src="care.faceUrl"/>
                        </div>
                        <ul class="show_bottom">
                            <img class="show_img2" src="../images/comments.png"/>
                            <li v-for="(comment,commentIndex) in item.commentList" :key="commentIndex" :data-replyid="comment.id" :data-moodid="item.id"
                                :data-userid="comment.fromuserid" data-ajaxresult="hasface" @click="commentOrDel(comment.fromuserid,comment.id,index,commentIndex)" v-if="!comment.isDel">
                                <img class="show_bottom_img" :src="comment.from_faceUrl">
                                <div class="show_bottom_text">
                                    <div class="reply_author">
                                        <a class="pname other" href="javascript:;">{{comment.from_nickName}}</a>
                                    </div>
                                    <div class="reply_content">
                                        <template v-if="comment.tomoodreplyid>0">
                                            <span class="text_comment">回复</span><a class="pname other"
                                                                                   href="javascript:;">{{comment.to_nickName}}：</a>
                                        </template>
                                        <span class="text_comment">{{comment.content}}</span>
                                    </div>
                                </div>
                            </li>

                        </ul>
                    </div>


                </div>


            </div>

        </v-scroll>
    </div>


</template>

<script>


    import chart from "./chart.vue"
    import banner from "./banner.vue"
    import scroll from './lib/scroll.vue';
    import wx from 'weixin-js-sdk';
    var myIndex = {
        template: '#myIndex'
    };
    export default {
        data() {
            return {
                user:{},
                counter: 1, //默认已经显示出15条数据 count等于一是让从16条开始加载
                num: 10,  // 一次显示多少条
                pageStart: 0, // 开始页数
                pageEnd: 0, // 结束页数
                listdata: [], // 下拉更新数据存放数组
                downdata: [],  // 上拉更多的数据存放数组
                currTime: xqzs.dateTime.getTimeStamp(),
                topImg:xqzs.mood.getTopImg(),
                chartData:[
                    {"days": ["1月1", "2", "3", "4", "5", "6", "7"], "moods": [3, 5, 9, 6, 4, 3, 5]},
                {"days": ["1月8", "9", "10", "11", "12", "13", "14"], "moods": [1, 3, 7, 6, 4, 2, 6]},
                {"days": ["1月15", "16", "17", "18", "19", "20", "21"], "moods": [7, 8, 9, 0, 4, 0, 5]},
                {"days": ["1月22", '23', "24", "25", "26", "27", "28"], "moods": [5, 1, 2, 3, 4, 5, 6]}

                ]
            }
        },
        methods: {
            commentOrDel:function (userId,id,index,commentIndex) {
                let vm = this;
                if(userId===vm.user.id){
                    vm._delComment(id,index,commentIndex);
                }else{
                    vm.addComment(id,index,commentIndex);
                }
            },
            _delComment(id,index,commentIndex){
                let vm = this;
                xqzs.weui.actionSheet("删除我的评论?","删除",function () {
                    ///删除操作
                    let url  = web.API_PATH+ "mood/reply/[userId]/"+id;
                    vm.$http.delete(url)
                        .then((data) => {
                            if (data.data.status === 1) {
                                vm.downdata[index].commentList[commentIndex].isDel = true;
                                vm.$set(vm.downdata, index, vm.downdata[index])
                            } else {
                                xqzs.weui.toast("fail", "删除失败", function () {
                                });
                            }
                        })
                        .catch((response) => {

                        });


                },function () {
                    //取消
                })
            },
            addComment(id,index,commentIndex){
                let vm = this;
                xqzs.mood.actionSheetEdit("取消","发送",function (v) {
                    vm.$http.put(web.API_PATH+'mood/reply/add',{"moodId":vm.downdata[index].id,"userId":null,"replyId":id,"content":v}).then(response => {
                        if(response.data.status===1){
                            xqzs.weui.toast("success","提交成功",function () {
                            });
                            vm.downdata[index].commentList.push(response.data.data.reply);
                            vm.$set(vm.downdata, index, vm.downdata[index])
                            console.log(response.data.data.reply)
                        }







                    }, response => {
                        // error
                    });
                    console.log(v)

                },function (v) {
                    console.log(v)
                    //取消
                },"回复 "+vm.downdata[index].commentList[commentIndex].from_nickName)
            },
            showComment: function (id, $index) {
                let vm = this;
                if (vm.downdata[$index].isShowComment) {
                    vm.downdata[$index].isShowComment = false;
                    vm.$set(vm.downdata, $index, vm.downdata[$index]);
                } else {
                    if (vm.downdata[$index].isAsk !== true) {

                        vm.$http.get(web.API_PATH + 'mood/care/query/comment/' + id).then((data) => {
                            vm.downdata[$index].isAsk = true;

                            if (data.data.status === 1) {

                                if (data.data.data.care.length > 0 || data.data.data.reply.length > 0) {
                                    vm.downdata[$index].hasComments = true;

                                    vm.downdata[$index].careList = data.data.data.care;
                                    vm.downdata[$index].commentList = data.data.data.reply;
                                    vm.downdata[$index].isShowComment = true;


                                }

                            }
                            console.log(data.data.data);
                            vm.$set(vm.downdata, $index, vm.downdata[$index]);
                        }, (response) => {
                            console.log('error');
                        });
                    }
                    vm.downdata[$index].isShowComment = true;
                    vm.$set(vm.downdata, $index, vm.downdata[$index]);
                }


            },

            empty: function (id, $index) {
                let vm = this;
                xqzs.weui.dialog("", "确定删除吗？", function () {

                }, function () {
                    vm._empty(id, $index);
                })
            },
            _empty: function (id, $index) {
                let vm = this;
                let url = web.API_PATH + "mood/clean/content/[userId]/" + id
                vm.$http.delete(url)
                    .then((data) => {
                        if (data.data.status === 1) {
                            vm.downdata[$index].content = '';
                            vm.$set(vm.downdata, $index, vm.downdata[$index])
                        } else {
                            xqzs.weui.toast("fail", "删除失败", function () {
                            });
                        }
                    })
                    .catch((response) => {

                    });

            },
            revoke: function (id, $index) {
                let vm = this;
                xqzs.weui.dialog("", "确定撤回吗？", function () {

                }, function () {
                    vm._revoke(id, $index);
                })
            },
            _revoke: function (id, $index) {
                let vm = this;
                let url = web.API_PATH + "mood/[userId]/" + id
                vm.$http.delete(url, {emulateJSON: true})
                    .then((data) => {
                        if (data.data.status === 1) {
                            vm.downdata[$index].hide = true;
                            vm.$set(vm.downdata, $index, vm.downdata[$index]);
                            xqzs.weui.toast("success", "撤回成功", function () {
                            });
                        } else {
                            xqzs.weui.toast("fail", "撤回失败", function () {
                            });
                        }
                        console.log(data);
                    })
                    .catch((data) => {

                    });

            },
            showBigImg: function (list, curr) {

                var current = curr.bigUrl;
                var $data = [];
                for (var i = 0; i < list.length; i++) {
                    $data.push(list[i].bigUrl);
                }

                wx.previewImage({
                    current: current, // 当前显示图片的http链接
                    urls: $data // 需要预览的图片http链接列表
                });

            },

            getList(){
                let vm = this;
                vm.$http.get(web.API_PATH + 'mood/query/user/page/[userId]/' + 1 + "/" + vm.num).then((response) => {
                    vm.downdata = response.data.data.rows;
                    vm.downdata = xqzs.mood.initMoodsData(vm.downdata);
                    console.log(vm.downdata);
                    vm.$nextTick(function () {
                        myResizePicture();//渲染完成
                    })
                }, (response) => {
                    console.log('error');
                });
            },
            onRefresh(done) {
                this.getList();
                done() // call done
            },
            onInfinite(done) {
                let vm = this;
                vm.$http.get(web.API_PATH + 'mood/query/user/page/[userId]/' + (vm.counter + 1) + "/" + vm.num).then((response) => {
                    vm.counter++;
                    vm.pageEnd = vm.num * vm.counter;
                    vm.pageStart = vm.pageEnd - vm.num;
                    let arr = response.data.data.rows;
                    let i = 0;
                    let end = vm.pageEnd;
                    arr = xqzs.mood.initMoodsData(arr);
                    for (; i < arr.length; i++) {
                        vm.downdata.push(arr[i]);
                    }
                    vm.$nextTick(function () {
                        myResizePicture();//渲染完成
                    });
                    if (arr.length === 0) {
                        this.$el.querySelector('.load-more').style.display = 'none';
                        this.$el.querySelector('.load-finish').style.display = 'block';
                        return;
                    }

                    done() // call done
                }, (response) => {
                    console.log('error');
                });
            }


        },
        mounted: function () {
            let _this = this;
            this.getList();

            this.$http({
                method: 'GET',
                type: "json",
                url: web.API_PATH + 'user/find/by/user/Id/[userId]',
            }).then(function (data) {//es5写法
                if (data.data.data !== null) {
                    _this.user = eval(data.data.data);
                }
            }, function (error) {
                //error
            });


            _this.$http.get(web.API_PATH + 'mood/get/user/mood/week/[userId]')
                .then((data) => {
                     if (data.data.status === 1) {
                        for(let i =0;i<data.data.data.length;i++){
                            let week = {days:[],moods:[]};
                            for(let j=0;j<data.data.data[i].length;j++){
                                week.days.push(data.data.data[i][j].day);
                                week.moods.push(data.data.data[i][j].value);
                            }

                            _this.$set(_this.chartData, i, week)


                        }



                         console.log( _this.chartData)
                    }
                })
                .catch((response) => {

                });






        },


        components: {
            'v-scroll': scroll, "v-chart": chart,"v-banner": banner
        }
    }


</script>
<style>

    .actionSheet_wrap .doAction{
        font-size: 16px;
        color: red;
    }
    .actionSheet_wrap .weui-actionsheet__title-text{
        color: #999;
        font-size: 13px;
        height: 40px;

    }

    /*mood item*/
    .show_box {
        width: 100%;
        background: #F9F9F9;
        border-radius: 5px;
        margin-top: 15px;
        position: relative
    }

    .show_box .arraw {
        background: #f9f9f9;
        width: 16px;
        height: 16px;
        position: absolute;
        top: -4px;
        left: 11px;
        transform: rotate(45deg);
    }

    .show_top {
        padding-left: 30px;
        padding-top: 7px;
        padding-bottom: 4px;
        overflow: auto;
        position: relative;
    }

    .show_top .show_img1 {
        position: absolute;
        top: 15px;
        left: 9px;
        width: 18px;
        height: auto;
        margin: 0;
    }

    .show_top img {
        width: 33px;
        height: 33px;
        float: left;
        margin-right: 5px;
        margin-bottom: 6px;
    }

    .show_bottom {
        position: relative;
        padding-left: 30px;
        padding-bottom: 4px;
        overflow: auto;
        padding-right: 12px;
        border-top: 1px solid #eeeeee
    }

    .show_img2 {
        width: 18px;
        position: absolute;
        top: 15px;
        left: 9px;
    }

    .show_bottom li {
        position: relative;
        padding-top: 5px;
        padding-left: 0;
        padding-right: 0;
        padding-bottom: 8px;
        border-bottom: 1px solid #eeeeee;
    }

    .show_bottom li:last-child {
        border-bottom: none;
    }

    .show_bottom_img {
        width: 33px;
        height: 33px;
        position: absolute;
        top: 7px;
        left: 0px;
    }

    .show_bottom_text {
        margin-left: 45px;
    }

    .show_bottom_text span {
        font-size: 13px;
        color: #333;
    }

    .show_bottom_text div {
        font-size: 13px;
        color: #333333;
        line-height: 20px;
    }

    .show_bottom_text .pname {
        color: #5e61a2;
        font-size: 13px;
    }

    .show_box .show_img1, .show_box .show_img2 {
        width: 14px;
    }

    .btn_del {
        display: inline-block;
        margin-left: 13px;
        color: #5e61a2;
        font-size: 12px;
    }

    .editMood {
        height: 34px;
        line-height: 34px;
        font-size: 14px;
        color: #999999;
        background: #dcdcdc;
        display: block;
        padding-left: 10px;
        border-radius: 5px;
        margin-bottom: 10px;
        position: relative;
    }

    .editMood img {
        position: absolute;
        top: 7px;
        right: 10px;
        width: 20px;
    }



    .index_banner {
        position: relative;
    }

    .userHeaderImg {
        height: 64px;
        width: 64px;
        position: absolute;
        left: 50%;
        bottom: -32px;
        margin-left: -32px;
        border-radius: 50%;
        background: rgba(255, 255, 255, 0.6);
        padding: 2px;
    }

    .userHeaderImg img {
        height: 64px;
        width: 64px;
        border-radius: 50%;
        margin: auto;
        display: block;
    }

    .chart_box {
        height: 190px;
        padding: 40px 0 20px 0;
        background: #ffffff;
        margin-bottom: 5px;
    }

    .myMood_list {
        background: #ffffff;
        padding: 16px 10px;
        margin-bottom: 1px;
    }

    .moodImg {
        width: 40px;
        height: 40px;
        display: block;
        float: left;
    }

    .moodImg_right {
        margin-left: 50px;
    }

    .moodState {
        font-size: 14px;
        margin-bottom: 6px;
    }

    .moodContext {
        font-size: 14px;
        color: #333333;
        line-height: 20px;
        margin-bottom: 10px;
    }

    .moodPhotoLists {
        display: flex;
        height: auto;
        margin-right: 20px;
        margin-bottom: 15px;
    }

    .moodPhotoList {
        float: left;
        margin-right: 0.3125rem;
        width: 4.8675rem;
        height: 4.8675rem;
        overflow: hidden;
        position: relative;
    }

    .moodLoc {
        font-size: 12px;
        color: #516591;
        margin-bottom: 5px;
    }

    .moodTime {
        font-size: 12px;
        color: #787878;
        height: 20px;
        line-height: 20px;
    }

    .moodFollow {
        float: right;
    }

    .followCount {
        float: left;
        margin-left: 5px;
    }

    .followtype {
        width: 24px;
        margin-top: -2px;
        margin-left: 5px;
        float: left;
    }

    .moodImg_right .moodPhotoLists.two > div {
        width: 7.5rem;
        height: 6rem;
    }

    .moodImg_right .moodPhotoLists.one > div {
        width: 10.7rem;
        height: 8.1rem;
    }


</style>


